import React, {Component} from 'react';
import Slider from 'react-slick';

export default class Blocks extends Component{
    constructor(props){
        super(props);

        this.state = {

        };

    }



    render(){

        let settings = {
            dots: false,
            infinite: true,
            draggable: false,
            speed: 500,
            slidesToShow: 10,
            slidesToScroll: 1,
        };

        return(
            <div className="blocks">
                <div className="blocksContainer">
                    <Slider {...settings}>
                        <div><h3>1</h3></div>
                        <div><h3>2</h3></div>
                        <div><h3>3</h3></div>
                        <div><h3>4</h3></div>
                        <div><h3>5</h3></div>
                        <div><h3>6</h3></div>
                    </Slider>
                </div>
            </div>
        );
    }
}